.grid {
 
  border:solid;
  
  box-sizing : border-box;
}
.grid {
  display: grid;
  grid-template-columns: 0.5fr 1.5fr 0.5fr ;
  grid-template-rows: 0.4fr 1fr 0.4fr  ;
}

.wrapper{
  box-sizing: border-box;
  border: solid;
  grid-column: 2 / 3;
  grid-row: 2 / 3 ;
} 

.wrapper{
  display: grid;
  grid-template-columns: 0.1fr 1fr 0.1fr 1fr 0.1fr;
  grid-template-rows: 0.1fr 1fr 0.1fr;

} 


.wrapper>.photo{
  grid-column: 2 / 3;
  grid-row: 2 / 3;
  border: solid;
  display: block;
  
}
.photo>img{
  
width: 100%;
height: 100%;
box-sizing: border-box;
}
.wrapper>.intro_box{
  grid-column: 4 / 5 ;
  grid-row: 2 / 3;
  border: dashed;
}

.intro_box{
  display: grid;
  grid-template-rows: 10% 20% 20% 20% 20% 10%;
}

.intro:nth-child(1) {
  grid-row: 2 / 3 ;
  text-align: center;
  font-size: 2.5vw;
  width:90%;
  margin-left: 5%;
  box-sizing: border-box;
  border:solid;
  line-height: 2.5;
}
.intro:nth-child(2) {
  grid-row: 4 / 6 ;
  width:90%;
  margin-left: 5%;
  text-align: center;
  font-size: 1.5vw;
 overflow: hidden;
 box-sizing: border-box;

}